
import XHeader from 'vux/src/components/x-header/index.vue'

import Progress from "@/components/progress";
import FilterSel from "../../components/filter";
import scroll from "../../components/scrollAll";
import { mapState } from "vuex";
export default {
data() {
return {
tabTextIndex: 0,
List: [],
Total: 0,
startDate: `${dayjs()
.startOf("month")
.format("YYYY-MM-DD 00:00:00")}`,
endDate: `${dayjs(dayjs().valueOf() + 24 * 60 * 60 * 1000).format(
"YYYY-MM-DD 00:00:00"
)}`,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
pageindex: 1,
selUserID: null,
defaultDate: {
beginDate: dayjs()
.startOf("month")
.format("YYYY-MM-DD"),
endDate: dayjs().format("YYYY-MM-DD")
},
endtimers: `${dayjs().format("YYYY-MM-DD 23:59:59")}`,
types: [
{
type: 1,
name: "选择分类",
notDict: true,
list: [
{
ID: 0,
name: "按成交总额统计"
},
{
ID: 1,
name: "按成交数量统计"
}
]
}
]
};
},
components: {
XHeader,
Progress,
FilterSel,
scroll
},
computed: {
...mapState(["CurrentUserID"]),
params() {
let userid =
this.selUserID === 0 || this.selUserID
? this.selUserID
: this.CurrentUserID;
return {
queryType: this.tabTextIndex + 1,
startDate: this.startDate,
endDate: this.endDate,
pageindex: this.pageindex,
selUserID: userid
};
}
},
filters: {
schedule(count, total) {
if (count === 0) {
return 0;
} else {
return Number.parseFloat(((count / total) * 100).toFixed(2));
}
}
},
methods: {

selectTime(date) {
let times = date["beginDate"].replace(/-/g, "/");
let tis = new Date(times).getTime();
let timee = date["endDate"].replace(/-/g, "/");
let tie = new Date(timee).getTime();
if (tis > tie) {
this.$vux.toast.show({
type: "warn",
text: "开始时间不能大于截止时间"
});
return;
}
if (!date["beginDate"]) {
this.$vux.toast.show({
type: "warn",
text: "请选择时间段"
});
return;
}
this.startDate = `${date["beginDate"]} 00:00:00`;
let end = `${timee} 00:00:00`;
end = Number.parseInt(new Date(end).getTime()) + 24 * 60 * 60 * 1000;
this.endDate = dayjs(end).format("YYYY-MM-DD 00:00:00");
this.endtimers = `${date["endDate"]} 23:59:59`;
this.changeParamsInit();
this.getList();
},
getList(pageIndex) {
this.$vux.loading.show({
text: "加载中"
});
const url = `/ProdStatis/GetProdStatis`;
axios.get(url, { params: this.params }).then(res => {
const data = res.data.Data;
if (pageIndex) {
this.List = [...this.List, ...data.List];
} else {
this.List = data.List;
}
this.Total = data.Total;
this.$refs.scroll.forceUpdate();
setTimeout(() => {
this.$vux.loading.hide();
}, 500);
});
},
listPullup() {
this.pageindex++;
this.getList(2);
this.$refs.scroll.scroll.finishPullUp();
this.$refs.scroll.refresh();
},
listPulldown() {
this.pageindex = 1;
this.getList();
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.refresh();
},

selectUser(userID) {
this.selUserID = userID;
this.changeParamsInit();
this.getList();
},
init() {
let hei = document.body.clientHeight;
this.$refs.clientList.style.height = `${hei - 77}px`;
this.pageindex = 1;
this.startDate = `${dayjs()
.startOf("month")
.format("YYYY-MM-DD 00:00:00")}`;
this.endDate = `${dayjs(dayjs().valueOf() + 24 * 60 * 60 * 1000).format(
"YYYY-MM-DD 00:00:00"
)}`;
this.defaultDate = {
beginDate: dayjs()
.startOf("month")
.format("YYYY-MM-DD"),
endDate: dayjs().format("YYYY-MM-DD")
};
this.selUserID = 0;
this.tabTextIndex = 0;
this.$refs["filterSele"].clearType();
this.getList();
},

changeGroup(v) {
this.tabTextIndex = v.ID;
this.changeParamsInit();
this.getList();
},
changeParamsInit() {
this.pageindex = 1;
this.$refs["scroll"].scrollTo(0, 0, 100);
},
toDetails(id, size) {
if (!size) {
return;
}
this.$router.push({
path: "/prodectDetails",
query: {
prodId: id,
beginDealDate: this.startDate,
endDealDate: this.endtimers,
createUserId: this.selUserID
}
});
}
},
mounted() {
this.init();
},
watch: {
$route(to, form) {
if (to.path === "/statisIndex") this.init();
}
}
};



